import cpfValidate from "./cpfValidate";

export default class GeraCPF {
  rand(min = 100000000, max = 999999999) {
    return String(Math.floor(Math.random() * (max - min) + min));
  }

  formatCpf(cpf) {
    return (
      cpf.slice(0, 3) +
      "." +
      cpf.slice(3, 6) +
      "." +
      cpf.slice(6, 9) +
      "-" +
      cpf.slice(9, 11)
    );
  }

  geraNovoCpf() {
    const cpfSemDigito = this.rand();
    const cpfDigito1 = cpfValidate.digitGenerator(cpfSemDigito);
    const cpfDigito2 = cpfValidate.digitGenerator(cpfDigito1);

    const checkValid = new cpfValidate(cpfDigito2).startAlgo();

    if (checkValid.valid === true) {
      return this.formatCpf(cpfDigito2);
    } else {
      return;
    }
  }
}
